import {Injectable} from '@angular/core';
import {AppClientService} from 'app/shared/services/app-client.service';
import {ISapAccountMapGetAllModel, ISapAccountMapModel, ISapAccountMapRequest,} from '../models/sap-account-map';
import {IResponse, IResponseList} from 'app/shared/interfaces/responces';
import {db} from 'app/shared/resources/database-tabels';
import {IPageParams} from 'app/shared/interfaces/params';

@Injectable({
  providedIn: "root",
})
export class SapAccountMapService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ISapAccountMapRequest) {
    return this.post<IResponse<ISapAccountMapModel>>(
      "SapB1AccountMapSetup/Create",
      data
    );
  }

  update(data: ISapAccountMapRequest) {
    return this.post<IResponse<ISapAccountMapModel>>(
      "SapB1AccountMapSetup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ISapAccountMapModel[]>>(
      "SapB1AccountMapSetup/GetList",
      {
        params: {
          ...params,
          $expand: db.PAY_Variable,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<ISapAccountMapGetAllModel[]>>(
      "SapB1AccountMapSetup/GetAll"
      ,{
        params: {
          $expand: `${db.SAP_IntegrationPurpose},${db.core_Org}/${db.Core_Org_Structure_Org}`
        },
      }
    // ).pipe(
    //   map((res: IResponse<ISapAccountMapGetAllModel[]>) => {
    //     return res.data;
    //   })
     );
  }

  getById(id: number) {
    return this.get<IResponse<ISapAccountMapGetAllModel[]>>(
      "SapB1AccountMapSetup/GetAll",
      {
        params: {
          $filter: `id eq ${id}`,
          $expand: `${db.SAP_IntegrationPurpose}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("SapB1AccountMapSetup/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
