import { msgTranslate } from "app/shared/resources/helpers-functions";
import { ISapLoanTypeAccountModel } from "../models/sap-loan-type-account";

export function sapLoanTypeToMap(
  sapLoanTypes: ISapLoanTypeAccountModel[]
): ISapLoanTypeAccountModel[] {
  return sapLoanTypes.map((sapLoanType) => {
    return <ISapLoanTypeAccountModel>{
      ...sapLoanType,
      sapSystem: msgTranslate(
        sapLoanType.saP_SystemInstance.ar_Name,
        sapLoanType.saP_SystemInstance.en_Name
      ),
      loanType: msgTranslate(
        sapLoanType.paY_Loan_TYPE.ar_Name,
        sapLoanType.paY_Loan_TYPE.en_Name
      ),
    };
  });
}
