import {
  ISapLoanTypeAccountModel,
  ISapLoanTypeAccountRequest,
} from "./../models/sap-loan-type-account";
import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class SapLoanTypeAccountService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ISapLoanTypeAccountRequest) {
    return this.post<IResponse<ISapLoanTypeAccountModel>>(
      "SapB1LoanTypeSetup/Create",
      data
    );
  }

  update(data: ISapLoanTypeAccountRequest) {
    return this.post<IResponse<ISapLoanTypeAccountModel>>(
      "SapB1LoanTypeSetup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ISapLoanTypeAccountModel[]>>(
      "SapB1LoanTypeSetup/GetList",
      {
        params: params,
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("SapB1LoanTypeSetup/Delete", {
      params: { Id: Id },
    });
  }
}
