import { Component, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { sapAccountToMap } from "app/sap-business-one-setup/mapper/sap-account-map";
import { ISapAccountMapModel } from "app/sap-business-one-setup/models/sap-account-map";
import { SapAccountMapService } from "app/sap-business-one-setup/services/sap-account-map.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-sap-account-map-index",
	templateUrl: "./sap-account-map-index.component.html",
	styleUrls: ["./sap-account-map-index.component.scss"],
})
export class SapAccountMapIndexComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	mandatoryColomns: string[] = [
		"Ser",
		"id",
		"variables",
		"integrationPurpose",
		"accountOrPartnerCode",
		"accountType",
		"actions",
	];
	optionalColomns: string[] = [
		"bothBankCash",
		"is_Aggregated",
		"is_vendor",
		"ignoreCostCenters",
		"defualtOcrCode1",
		"defualtOcrCode2",
		"defualtOcrCode3",
		"defualtOcrCode4",
		"defualtOcrCode5",
		"defualtProj_Code",
		"udF1_Name",
		"udF1_Value",
		"udF2_Name",
		"udF2_Value",
		"udF3_Name",
		"udF3_Value",
	];

	dataSource: MatTableDataSource<ISapAccountMapModel>;
	isLoaded: boolean = false;
	constructor(public sapAccountMapService: SapAccountMapService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);

		this.form = this.fb.group({
			systemInstance: [null],
			integrationPurpose_ID: [null],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	searchList() {
		if (!this.form?.value.integrationPurpose_ID) this.warning("chooseIntegrationPurpose");
		else {
			let filter = {
				$filter: `IntegrationPurpose_ID eq ${this.form.value.integrationPurpose_ID}`,
			};
			this.getList(this.firstPage, filter);
		}
	}

	getList(page: number, filter = {}, pageSize?: number) {
		this.removePageParams();
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
			...filter,
		};
		this.sapAccountMapService
			.getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<ISapAccountMapModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					if (res.data.length == 0 && page > 1) {
						this.getList(this.prevPage);
						return;
					}
					this.dataSource = new MatTableDataSource(sapAccountToMap(res.data));
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	onDelete(sapAccountMap: ISapAccountMapModel): void {
		let msg = this.msgTranslate(`${sapAccountMap.variables} - ${sapAccountMap.integrationPurpose}`);
		this.deleteDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(sapAccountMap);
		});
	}

	delete(sapAccountMap: ISapAccountMapModel) {
		this.showLoading();
		this.sapAccountMapService.Delete(sapAccountMap.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.pageSize);
				}
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	resetSearch() {
		this.form.reset();
		this.getList(this.firstPage);
	}
}
