import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { sapLoanTypeToMap } from "app/sap-business-one-setup/mapper/sap-loan-type";
import {
  ISapLoanTypeAccountModel,
  ISapLoanTypeAccountRequest,
} from "app/sap-business-one-setup/models/sap-loan-type-account";
import { SapLoanTypeAccountService } from "app/sap-business-one-setup/services/sap-loan-type-account.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-sap-loan-type-account",
	templateUrl: "./sap-loan-type-account.component.html",
	styleUrls: ["./sap-loan-type-account.component.scss"],
})
export class SapLoanTypeAccountComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = ["Ser", "id", "debitAccount", "sapSystem", "loanType", "actions"];
	optionalColomns: string[] = ["creditAccount", "isVendor"];

	dataSource: MatTableDataSource<ISapLoanTypeAccountModel>;
	isLoaded: boolean = false;
	constructor(public sapLoanTypeAccountService: SapLoanTypeAccountService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);

		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			sapSystemId: [null, [Validators.required]],
			loanTypeId: [null, [Validators.required]],
			debitAccount: [null, [Validators.required, CustomValidator.whiteSpace]],
			creditAccount: [null, [Validators.required, CustomValidator.whiteSpace]],
			isVendor: [null],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId() {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.sapLoanTypeAccountService
			.getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<ISapLoanTypeAccountModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(sapLoanTypeToMap(res.data));
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: ISapLoanTypeAccountRequest = {
				ID: this.form.value.id,
				SapSystemId: this.form.value.sapSystemId,
				LoanTypeId: this.form.value.loanTypeId,
				DebitAccount: this.form.value.debitAccount,
				CreditAccount: this.form.value.creditAccount,
				IsVendor: this.form.value.isVendor ?? false,
			};

			model.ID == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: ISapLoanTypeAccountRequest) {
		this.showLoading();
		this.sapLoanTypeAccountService
			.create(model)
			.subscribe((res: IResponse<ISapLoanTypeAccountModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.getList(this.lastPage, this.page.pageSize);
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	update(model: ISapLoanTypeAccountRequest) {
		this.showLoading();
		this.sapLoanTypeAccountService
			.update(model)
			.subscribe((res: IResponse<ISapLoanTypeAccountModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.clearForm();
					this.getList(this.currentPage, this.page.pageSize); // current page after update
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	sapLoanTypeAccountToEdit(sapLoanTypeAccount: ISapLoanTypeAccountModel) {
		this.form.patchValue(sapLoanTypeAccount);
		// console.log(this.form.value);
	}

	onDelete(sapLoanTypeAccount: ISapLoanTypeAccountModel): void {
		let msg = this.msgTranslate(sapLoanTypeAccount.debitAccount);
		this.deleteDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(sapLoanTypeAccount);
		});
	}

	delete(sapLoanTypeAccount: ISapLoanTypeAccountModel) {
		this.showLoading();
		this.sapLoanTypeAccountService.Delete(sapLoanTypeAccount.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (sapLoanTypeAccount.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
