import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { sapAccountGetAllToMap } from "app/sap-business-one-setup/mapper/sap-account-map";
import {
  ISapAccountMapGetAllModel,
  ISapAccountMapModel,
  ISapAccountMapRequest,
} from "app/sap-business-one-setup/models/sap-account-map";
import { SapAccountMapService } from "app/sap-business-one-setup/services/sap-account-map.service";
import { RadioButtonModel } from "app/shared/components/resources/dynamic-form-field/radio-button/radio-button.component";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-add-sap-account-map",
	templateUrl: "./add-sap-account-map.component.html",
	styleUrls: ["./add-sap-account-map.component.scss"],
})
export class AddSapAccountMapComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;

	// sapAccountMap: ISapAccountMapModel;
	sapAccountMapId: number;

	constructor(private sapAccountMapService: SapAccountMapService) {
		super();
	}

	ngOnInit() {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			systemInstanceId: [null, [Validators.required]],
			integrationPurpose_ID: [null, [Validators.required]],
			payment_ID: [null, [Validators.required]],
			variable_ID: [null, [Validators.required]],
			accountOrPartnerCode: [null, [Validators.required, CustomValidator.whiteSpace]],
			is_vendor: [null],
			is_Aggregated: [true],
			ignoreCostCenters: [null],
			organizationTypeId: [null],
			organization_ID: [null],
			// for radio btn (job title(true) / job families(false) / none(null))
			isJobtitleType: [true],
			jobTitle_ID: [null, [Validators.required]],
			job_Title_Family_ID: [null],
			bothBankCash: [true],
			bank_ID: [null],
			is_Debit: [true],
			defualtOcrCode1: [null],
			defualtOcrCode2: [null],
			defualtOcrCode3: [null],
			defualtOcrCode4: [null],
			defualtOcrCode5: [null],
			defualtProj_Code: [null],
			udF1_Name: [null],
			udF1_Value: [null],
			udF2_Name: [null],
			udF2_Value: [null],
			udF3_Name: [null],
			udF3_Value: [null],
		});

		this.activatedRoute.paramMap.subscribe((params) => {
			this.sapAccountMapId = Number(params.get("sapAccountMapId")) ?? 0;
			// if (this.sapAccountMapId > 0 && this.CheckNoBusinessOrCompany())
			//   this.getById();
		});
	}

	override onChangeBusinessAndCompanyId(): void {
		// this.sapAccountMap = null;
		if (this.sapAccountMapId > 0 && this.checkNoBusinessOrCompany()) this.getById();
	}

	get formCtrls() {
		return this.form.controls;
	}

	getById() {
		this.showLoading();
		this.sapAccountMapService
			.getById(this.sapAccountMapId)
			.subscribe((res: IResponse<ISapAccountMapGetAllModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
          let result: ISapAccountMapModel[] = sapAccountGetAllToMap(res.data);
          this.sapAccountMapToEdit(result[0]);
        } else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	sapAccountMapToEdit(sapAccountMap: ISapAccountMapModel) {
		this.form.patchValue(sapAccountMap);
		// dropdown filtered by another
		this.formCtrls.integrationPurpose_ID.setValue(sapAccountMap.integrationPurpose_ID);
		this.formCtrls.variable_ID.setValue(sapAccountMap.variable_ID);
		this.formCtrls.organization_ID.setValue(sapAccountMap.organization_ID);
		setTimeout(() => {
			this.formCtrls.jobTitle_ID.setValue(sapAccountMap.jobTitle_ID);
			this.formCtrls.job_Title_Family_ID.setValue(sapAccountMap.job_Title_Family_ID);
		}, 20);
		// job radio buttons
		let isJobRadio = null;
		if (sapAccountMap.job_Title_Family_ID) isJobRadio = false;
		else if (sapAccountMap.jobTitle_ID) isJobRadio = true;
		this.formCtrls.isJobtitleType.setValue(isJobRadio);
		this.onChangeIsJobRadio(isJobRadio, true);
		// console.log(this.form.value);
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: ISapAccountMapRequest = {
				ID: this.form.value.id,
				IntegrationPurpose_ID: this.form.value.integrationPurpose_ID,
				Payment_ID: this.form.value.payment_ID,
				Variable_ID: this.form.value.variable_ID,
				AccountOrPartnerCode: this.form.value.accountOrPartnerCode,
				Is_vendor: this.form.value.is_vendor ?? false,
				Is_Aggregated: this.form.value.is_Aggregated ?? false,
				IgnoreCostCenters: this.form.value.ignoreCostCenters ?? false,
				Organization_ID: this.form.value.organization_ID,
				JobTitle_ID: this.form.value.jobTitle_ID,
				Job_Title_Family_ID: this.form.value.job_Title_Family_ID,
				BothBankCash: this.form.value.bothBankCash ?? false,
				Bank_ID: this.form.value.bank_ID,
				Is_Debit: this.form.value.is_Debit ?? false,
				DefualtOcrCode1: this.form.value.defualtOcrCode1,
				DefualtOcrCode2: this.form.value.defualtOcrCode2,
				DefualtOcrCode3: this.form.value.defualtOcrCode3,
				DefualtOcrCode4: this.form.value.defualtOcrCode4,
				DefualtOcrCode5: this.form.value.defualtOcrCode5,
				DefualtProj_Code: this.form.value.defualtProj_Code,
				UDF1_Name: this.form.value.udF1_Name,
				UDF1_Value: this.form.value.udF1_Value,
				UDF2_Name: this.form.value.udF2_Name,
				UDF2_Value: this.form.value.udF2_Value,
				UDF3_Name: this.form.value.udF3_Name,
				UDF3_Value: this.form.value.udF3_Value,
			};

			model.ID == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: ISapAccountMapRequest) {
		this.showLoading();
		this.sapAccountMapService.create(model).subscribe((res: IResponse<ISapAccountMapModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.changeDataAfterAddAndEdit(res.data);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: ISapAccountMapRequest) {
		this.showLoading();
		this.sapAccountMapService.update(model).subscribe((res: IResponse<ISapAccountMapModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				this.changeDataAfterAddAndEdit(res.data);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	changeDataAfterAddAndEdit(data: ISapAccountMapModel) {
		this.sapAccountMapId = data.id;
		this.formCtrls.id.setValue(this.sapAccountMapId);
		let url = `/sap-business-one-setup/${this.moduleId}/sap-account-map/edit/${this.sapAccountMapId}`;
		this.navigateTo(url);
		// this.sapAccountMap = data;
		this.formCtrls.is_Aggregated.setValue(data.is_Aggregated);
		// this.sapAccountMap.systemInstanceId = this.formCtrls.systemInstanceId.value;
		// this.sapAccountMap.organizationTypeId =
		//   this.formCtrls.organizationTypeId.value;
	}

	clearForm() {
		// if (this.sapAccountMapId == 0)
		this.form.reset({
			id: 0,
			is_Aggregated: true,
			isJobtitleType: true,
			bothBankCash: true,
			is_Debit: true,
		});
		// else this.sapAccountMapToEdit();
		this.submitted = false;
	}

	onChangeIsVendor(value: boolean) {
		if (value) this.formCtrls.is_Aggregated.setValue(false);
	}

	onChangeIsAggregated(value: boolean) {
		if (value) this.formCtrls.is_vendor.setValue(false);
	}

	onChangeIsJobRadio(value: boolean, firstTime: boolean = false) {
		if (!firstTime) {
			this.formCtrls.jobTitle_ID.reset();
			this.formCtrls.job_Title_Family_ID.reset();
		}
		if (value == true) {
			this.formCtrls.jobTitle_ID.setValidators([Validators.required]);
			this.formCtrls.job_Title_Family_ID.clearValidators();
		} else if (value == false) {
			this.formCtrls.job_Title_Family_ID.setValidators([Validators.required]);
			this.formCtrls.jobTitle_ID.clearValidators();
		} else {
			this.formCtrls.jobTitle_ID.clearValidators();
			this.formCtrls.job_Title_Family_ID.clearValidators();
		}
		this.formCtrls.jobTitle_ID.updateValueAndValidity();
		this.formCtrls.job_Title_Family_ID.updateValueAndValidity();
	}

	onChangeIsBankRadio(value: boolean) {
		if (!value) {
			this.formCtrls.bank_ID.setValue(null);
		}
	}

	jobTitleRadioList: RadioButtonModel[] = [
		{
			value: true,
			label: "jobTitle",
			cssClass: "col-md-4 col-12",
		},
		{
			value: false,
			label: "jobFamily",
			cssClass: "col-md-4 col-12",
		},
		{
			value: null,
			label: "none",
			cssClass: "col-md-4 col-12",
		},
	];

	bankRadioList: RadioButtonModel[] = [
		{
			value: true,
			label: "bothBankWithInstruction",
		},
		{
			value: false,
			label: "cash",
		},
	];

	isDebitRadioList: RadioButtonModel[] = [
		{
			value: true,
			label: "debit",
		},
		{
			value: false,
			label: "credit",
		},
	];
}
