import { msgTranslate } from "app/shared/resources/helpers-functions";
import {
  ISapAccountMapGetAllModel,
  ISapAccountMapModel,
} from "../models/sap-account-map";

export function sapAccountGetAllToMap(
  violations: ISapAccountMapGetAllModel[]
): ISapAccountMapModel[] {
  return violations.map((sapAccount) => {
    return <ISapAccountMapModel>{
      ...sapAccount._I_PAY_Variable_SAP_SystemInstance_AccountNumber_COMM_Bank,
      organizationTypeId: sapAccount.orgTypeId,
      systemInstanceId:
        sapAccount._I_PAY_Variable_SAP_SystemInstance_AccountNumber_COMM_Bank
          .saP_IntegrationPurpose?.systemInstance_ID,
    };
  });
}

export function sapAccountToMap(
  violations: ISapAccountMapModel[]
): ISapAccountMapModel[] {
  return violations.map((sapAccount) => {
    return <ISapAccountMapModel>{
      ...sapAccount,
      integrationPurpose: sapAccount.saP_IntegrationPurpose.name,
      accountType: sapAccount.is_Debit ? "Debit" : "Credit",
      variables: msgTranslate(
        sapAccount.pay_Variable?.arName,
        sapAccount.pay_Variable?.name
      ),
      bothBankCash:
        sapAccount.bothBankCash && !sapAccount.bank_ID ? true : false,
    };
  });
}
