import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { SystemInstanceIndexComponent } from "./components/system-instance/system-instance-index/system-instance-index.component";
import { AddSystemInstanceComponent } from "./components/system-instance/add-system-instance/add-system-instance.component";
import { AddSapAccountMapComponent } from "./components/sap-account-map/add-sap-account-map/add-sap-account-map.component";
import { SapAccountMapIndexComponent } from "./components/sap-account-map/sap-account-map-index/sap-account-map-index.component";
import { SapLoanTypeAccountComponent } from "./components/sap-loan-type-account/sap-loan-type-account.component";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ViewsManager } from "app/shared/resources/views-manager";

const routes: Routes = [
	{
		path: "system-instance",
		data: { view:ViewsManager.system_instance },
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: SystemInstanceIndexComponent,
				data: { view: ViewsManager.system_instance_index},
			},
			{
				path: "add",
				component: AddSystemInstanceComponent,
				data: { view: ViewsManager.add_system_instance },
			},
			{
				path: "edit/:systemInstanceId",
				component: AddSystemInstanceComponent,
				data: { view: ViewsManager.add_system_instance},
			},
		],
	},
	{
		path: "sap-account-map",
		data: { view:  ViewsManager.sap_account_map },
		children: [
			{
				path: "",
				redirectTo: "index",
				pathMatch: pathMatch.full,
			},
			{
				path: "index",
				component: SapAccountMapIndexComponent,
				data: { view:  ViewsManager.sap_account_map_index },
			},
			{
				path: "add",
				component: AddSapAccountMapComponent,
				data: { view:  ViewsManager.add_sap_account_map},
			},
			{
				path: "edit/:sapAccountMapId",
				component: AddSapAccountMapComponent,
				data: { view:  ViewsManager.add_sap_account_map },
			},
		],
	},
	{
		path: "sap-loan-type-account",
		component: SapLoanTypeAccountComponent,
		data: { view:  ViewsManager.sap_loan_type_account },
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class SapBusinessOneSetupRoutingModule {}
